.container {
  height: 100vh;
  padding: 20px;
  display: flex;
  box-sizing: border-box;
}

.left {
  width: 200px;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.list {
  padding-top: 8px;
  flex: 1;
  overflow-y: auto;
}

.list > div {
  padding: 5px 8px;
  cursor: pointer;
}

.content {
  height: 100%;
  flex: 1;
  overflow-y: auto;
  padding-left: 20px;
}
