.container {
  display: flex;
  padding: 15px;
  box-sizing: border-box;
}

.leftContent {
  flex: 1;
  height: calc(100vh - 30px);
  overflow-y: auto;
}

.rightNav {
  width: 100px;
  height: calc(100vh - 30px);
  overflow-y: auto;
}

.wrapIndustry div {
  line-height: 25px;
}

.tagStyle {
  font-size: 14px;
  cursor: pointer;
  display: inline-block;
  width: 100px;
  line-height: 25px;
  margin-right: 6px;
  margin-bottom: 5px;
  border: 1px solid #d9d9d9;
  padding: 0 7px;
  border-radius: 3px;
}

.gotoBtn {
  width: 30px;
}

.paddingRight20 {
  margin-left: 20px;
  color: #e2682b;
  font-size: 16px;
}

.paddingRight40 {
  margin-left: 40px;
  font-size: 14px;
}

.paddingRight60 {
  margin-left: 60px;
}
